import React, { Component } from "react";
import { withRouter } from "react-router-dom";

class NavBar extends Component {
  state = { showButton: false };
  toggleButton = () => {
    this.setState({
      showButton: !this.state.showButton,
    });
  };
  render() {
    const show = this.state.showButton ? "show" : "";
    return (
      <nav className="navbar navbar-expand-lg navbar-dark bg-primary">
        <a className="navbar-brand" href="/siyuan-robert-yu/#/">
          Robert's Home
        </a>
        <button
          // style={{ zIndex: 100, position: "relative" }}
          className="navbar-toggler"
          type="button"
          onClick={this.toggleButton}
          // data-toggle="collapse"
          // data-target="#navbarColor01"
          // aria-controls="navbarColor01"
          // aria-expanded="false"
          // aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className={"collapse navbar-collapse " + show} id="navbarColor01">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item">
              <a className="nav-link" href="/siyuan-robert-yu/#/personal">
                Personal
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="/siyuan-robert-yu/#/professional">
                Professional
              </a>
            </li>
          </ul>
        </div>
      </nav>
    );
  }
}

